//Variables
@import '../../styleV.scss';
.cvpage.container {
    
    width: $A4_width;
    background: $CV_background;
    border: 15px solid $CV_border;
    min-height: 700px;
    padding:0 !important;
    @include mobile-only() {
        width:100%;
        .brand  {
            flex-direction: column;
        }
        .brand_avatar {
            margin: 0 auto;
            margin-bottom : 25px;
        }
        .section_order {
            display:none;
        }
        .fullname {
            text-decoration: underline;
        }
        .ev_t {
            display:none;
        }
        .education_description {
            flex:1;
        }
    }
}
.brand {
    padding-left:15px;
    margin-top: 25px;
}
.brand_avatar {
    width:75px;
    
}
.cv_margin {
    margin-top:52px;
}
.brand_contact {
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    div {
        background: $black;
        padding:8px 10px;
        color:$white;
        font-size:15px;
    }
}
.fix_content {
    display: flex;
    flex-direction: row;
    padding: 0 15px;
}
.section_content {
    width:100%;
}
.section_order {
    width:75px;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 20px;
    position: relative;
    text-align: center;
    &:after {
        content: '';
        display:block;
        position: absolute;
        top:30px;
        left:-2px;
        right: 0;
        margin: 0 auto;
        height: .14em;
        width: 35px;
        background: $black;
    }
}
.fullname {
    font-size: 25px;
    font-weight: bold;
}
.introduce {
    font-size : 13px;
    font-weight: bold;
    text-align: left; 
}
.skill {
    margin: 20px 0;
    img {
        width:50px;
        margin-right: 12px;
        height:auto;
    }
    width:50%;
    font-weight: bold;
}
.education_description {
    width:74%;
}
.ev_t {
    flex:1;
    justify-content: end;
    text-align: end;
    font-weight: bold;
}
.ed_title {
    font-size: 17px;
    font-weight: bold;
    margin-bottom: 0;
}
.sub_desc {
    color: #A0A0A0;
}
.fix_desc {
    color: #464545 !important;
    padding-left:20px;
    word-break: break-word;
    font-size:15px;
    font-weight: 400;
    margin-top: 8px;
}
.educations {
    margin-top:10px;
    .education {
        margin-bottom:20px;
    }
}