//Variables
@import '../styleV.scss';
.header {
    width: 100%;
    height: $Header_height;
    position: relative;
    
}
.header_logo {
    width: $Header_logo__width;
    height: auto;
    @extend %auto_center;
    @include mobile-only() {
        width: 3rem;
        margin: 0 !important;
        right: 10px;
        top: .3rem;
        left:inherit;
    }
}
@include mobile-only() {
    .navbar-nav:first-child  {
        flex-direction: row;
    }
    .navbar-nav:last-child {
        display:none;
    }
}
.header_menu {
    padding: 0;
    @include mobile-only() {
        flex-wrap: nowrap;
    }
}
.header_menu_item {
    width: $Header_menu_item__width;
    margin-right: 20px;
    text-align: center;
    position: relative;
    a {
        text-decoration: none;
        color : $Header_menu_item__color;
    }
    &:after {
        content : '';
        width: 75%;
        position: absolute;
        margin: 0 auto;
        left:0;
        right: 0;
        opacity: 0;
        transition: 0.5s all; 
        bottom: -0.6rem;
        height: $Header_menu_item_underline__height;
        background: $Header_menu_item_underline__background;
    }
    &:hover, &.active {
        &:after {
            transition: 0.5s all;
            opacity: 1;
        }
    }
}
.right-item {
    @extend .header_menu_item;
    &:hover:after {
        display:none;
    }
    width: auto;
    margin-left:20px;
    span {
        font-size: 0.8rem;
        margin-left: 10px;
    }
}
.header_icon {
    width: $Header_icon_width;
    height: $Header_icon_width;
}