//Variables
@import 'styleV.scss';

.icon {
    &.clickable:hover {
        cursor:pointer;
    }
    width:$icon_width;
    height:$icon_width;
}
.bg-black {
    background: $black;
}
.btn-read {
    background: $black;
    padding: 10px 15px;
    color: $white !important;
    border-radius: 2px;
    font-size: 13px; 
}
a.btn-read {
    text-decoration: none;
}
.text-bold {
    font-weight: bold;
}