//Variables
@import '../../styleV.scss';
.toolbox_title {
    position: relative;
    &:after {
        content: '';
        display: block;
        bottom: -0.8rem;
        width:100%;
        height: $Tutorial_toolbox_underline_height;
        position: absolute;
        background: $Tutorial_toolbox_underline_background;
    }
}
.toolbox_list {
    margin-top: 2rem;
    li {
        margin-top:.5rem;
    }
}
.post {
    position: relative;
    padding: 10px;
    margin-bottom: 50px;
}
.post_description {
    margin: 2rem 0;

}
.post_order {
    position: absolute;
    top:0;
    left:-50px;
    width:40px;
    height:40px;
    background :#C4C4C4;
}